import React, { useState, useEffect } from "react";
import { TEXT } from "../Container/text";
import Timer from "../Timer/Timer";
import Card from "../Card/Card";
import * as XLSX from "xlsx";

import "./ExcelReader.css";

const ExcelReader = () => {
  const [data, setData] = useState([]);
  const [downloadAll, setDownloadAll] = useState(false);
  const [paper, setPaper] = useState(TEXT.Physics);
  const [start, setStart] = useState(0);
  const [numOfQuestion, setNumOfQuestion] = useState(5);
  const [screenType, setScreenType] = useState("shorts");
  const [language, setLanguage] = useState(TEXT.ENGLISH);
  const [bodyFontSize, setBodyFontSize] = useState(20);
  const [isPresenting, setIsPresenting] = useState(true);
  const [showAnswer, setShowAnswer] = useState(false);

  const handleFileUpload = (event) => {
    const file = event.target.files[0];
    const reader = new FileReader();

    reader.onload = (e) => {
      const binaryStr = e.target.result;
      const workbook = XLSX.read(binaryStr, { type: "binary" });
      const sheetName = workbook.SheetNames[0];
      const worksheet = workbook.Sheets[sheetName];
      const jsonData = XLSX.utils.sheet_to_json(worksheet);
      setData(jsonData);
    };

    reader.readAsBinaryString(file);
  };

  const handlePaperType = (text) => {
    setPaper(text);
  };
  const handleStart = (number) => {
    setStart(number);
  };

  const handleQuestionChange = (numOfQuestion) => {
    setNumOfQuestion(numOfQuestion);
  };

  const handleScreenType = (type) => {
    setScreenType(type);
  };

  const handleLanguage = (language) => {
    setLanguage(language);
  };

  const handleBodyFontSize = (fontSize) => {
    setBodyFontSize(fontSize);
  };

  useEffect(() => {
    if (data) {
      console.log("data => ", data);
    }
  }, [data]);

  return (
    <>
      <div className="excel-reader-container">
        <div className="nav-bar">
          <input type="file" accept=".xlsx, .xls" onChange={handleFileUpload} />

          <button
            className="btn btn-primary"
            onClick={() => setDownloadAll(true)}
          >
            Download All
          </button>

          <label htmlFor="paper-type">Paper Type:</label>
          <select
            id="paper-type"
            name="paper-type"
            onChange={(event) => {
              handlePaperType(event.target.value);
            }}
          >
            <option value={TEXT.Physics}>{TEXT.Physics}</option>
            <option value={TEXT.Chemistry}>{TEXT.Chemistry}</option>
            <option value={TEXT.Biology}>{TEXT.Biology}</option>
            <option value={TEXT.Math}>{TEXT.Math}</option>
            <option value={TEXT.Reasoning}>{TEXT.Reasoning}</option>
          </select>
          <label htmlFor="startFrom">Start from</label>
          <input
            id="startFrom"
            className="inputField"
            type="number"
            value={start}
            onChange={(event) => {
              handleStart(event.target.value);
            }}
            // placeholder="Start from"
            min={0}
          />

          <label htmlFor="questionCount">No. of question</label>
          <input
            id="questionCount"
            className="inputField"
            type="number"
            value={numOfQuestion}
            onChange={(event) => {
              handleQuestionChange(event.target.value);
            }}
            min={1}
          />

          {/* SCREEN RESOLUATION */}
          <label htmlFor="screen-type">Screen Type:</label>
          <select
            id="screen-type"
            name="screen-type"
            onChange={(event) => {
              handleScreenType(event.target.value);
            }}
          >
            <option value="shorts">Shorts</option>
            <option value="videos">Videos</option>
          </select>

          {/* SELECH LANGUAGE */}
          <label htmlFor="screen-type">Language :</label>
          <select
            id="screen-type"
            name="screen-type"
            onChange={(event) => {
              handleLanguage(event.target.value);
            }}
          >
            <option value={TEXT.ENGLISH}>{TEXT.ENGLISH}</option>
            <option value={TEXT.HINDI}>{TEXT.HINDI}</option>
            <option value={TEXT.HINGLISH}>{TEXT.HINGLISH}</option>
          </select>
          {/* FONT SIZE CONTROL */}
          <label htmlFor="fontSize">Body font size</label>
          <input
            id="fontSize"
            className="inputField"
            type="number"
            value={bodyFontSize}
            onChange={(event) => {
              handleBodyFontSize(event.target.value);
            }}
            min={1}
          />

          <button
            className={`btn ${isPresenting ? "btn-success" : "btn-danger"}`}
            onClick={() => setIsPresenting(!isPresenting)}
          >
            {isPresenting ? "Presentation ON" : "Presentation OFF"}
          </button>
        </div>
        <div className="d-flex justify-content-center align-items-start">
          {/* {data &&
            data.map((item, index) => {
              if (index >= start && index < +numOfQuestion + +start) {
                return ( */}
          <button
            className="btn btn-secondary m-2"
            onClick={() => (start > 0 ? setStart(+start - 1) : "")}
          >
            Left
          </button>
          <div>
            {isPresenting && (
              <Card
                data={data.length ? data[start] : ""}
                start={start}
                setStart={setStart}
                dataLength={data.length}
                isPresenting={isPresenting}
                // Qno={isPresenting ? start + 1 : index + 1 - start}
                Qno={+start + 1}
                downloadAll={downloadAll}
                setDownloadAll={setDownloadAll}
                paper={paper}
                screenType={screenType}
                showAnswer={showAnswer}
                setShowAnswer={setShowAnswer}
                language={language}
                bodyFontSize={bodyFontSize}
              />
            )}

            {/* <Card
                    data={item}
                    Qno={index + 1 - start}
                    downloadAll={downloadAll}
                    setDownloadAll={setDownloadAll}
                    paper={paper}
                    screenType={screenType}
                    showAnswer={true}
                    language={language}
                    bodyFontSize={bodyFontSize}
                  /> */}
            {!isPresenting &&
              data.map((item, index) => {
                if (index < numOfQuestion + +start && index >= start) {
                  return ["Q", "A"].map((res) => (
                    <Card
                      data={item}
                      Qno={+index + 1 - +start}
                      isPresenting={isPresenting}
                      downloadAll={downloadAll}
                      setDownloadAll={setDownloadAll}
                      paper={paper}
                      screenType={screenType}
                      showAnswer={res == "A" ? true : false}
                      language={language}
                      bodyFontSize={bodyFontSize}
                    />
                  ));
                }
              })}
          </div>
          <button
            className="btn btn-secondary m-2"
            onClick={() =>
              start < data.length - 1 ? setStart(+start + 1) : ""
            }
          >
            Right
          </button>
          {/* // ); // } // })} */}
        </div>
      </div>
    </>
  );
};

export default ExcelReader;
