import React, { useState, useEffect } from "react";
import "bootstrap/dist/css/bootstrap.min.css"; // Ensure Bootstrap CSS is imported

function Timer({ start, setStart, dataLength, showAnswer, setShowAnswer }) {
  const [time, setTime] = useState(45); // Initialize the timer to 30 seconds

  useEffect(() => {
    // Set up the interval for the countdown
    if (time > 0) {
      const timerId = setInterval(() => {
        setTime((prevTime) => prevTime - 1);
      }, 1000); // Decrease the time every second

      // Cleanup function to clear the interval when the component unmounts or time changes
      return () => clearInterval(timerId);
    }

    if (!time) {
      //   setStart(start < dataLength - 1 ? start + 1 : "");
      if (start < dataLength - 1) {
        // setStart(start + 1);
        setShowAnswer(!showAnswer);
        if (showAnswer) {
          setStart(start + 1);
          //   setTime(30);
        } else {
          setTime(10);
        }
      }
    }
  }, [time]);

  return (
    <div className="d-flex align-items-center justify-content-center">
      <div className="card text-center">
        <div className=" d-flex p-2">
          <h5 className=" m-0">
            {showAnswer
              ? "Next question in : " + time + " sec"
              : "Time Left : " + time + " sec"}
          </h5>

          {/* {time === 0 && <h2 className="mt-3 text-danger">Time's up!</h2>} */}
        </div>
      </div>
    </div>
  );
}

export default Timer;
